import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../../actions';
import { Field, reduxForm ,formValueSelector  } from 'redux-form';
import moment from 'moment';

import { withStyles } from 'material-ui/styles';
import classNames from 'classnames';
import Typography from 'material-ui/Typography';

import Grid from 'material-ui/Grid';
import IconButton from 'material-ui/IconButton';
import MenuIcon from 'material-ui-icons/Menu';


import Icon from 'material-ui/Icon';
import { InputAdornment } from 'material-ui/Input';
import Divider from 'material-ui/Divider';
import ExpansionPanel, {
  ExpansionPanelSummary,
  ExpansionPanelDetails,
} from 'material-ui/ExpansionPanel';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';
import validate from './validate';
import renderField from './renderField';

import Paper from 'material-ui/Paper';
import DatePicker from 'rc-calendar/lib/Picker';
import Calendar from 'rc-calendar';
import Input, { InputLabel } from 'material-ui/Input';

import {upper, normalizePhone,onlyNums, onlyDigits,nationalID} from '../../utils/normalizer';
import {loanFrequency} from '../../utils/const';

const requiredFields = [
    'guarantor_name',
    

  ]
const format = 'YYYY-MM-DD HH:mm:ss';

function getFormat(time) {
  return time ? format : 'YYYY-MM-DD';
}

const styles = theme => ({

	margin: {
    	margin: theme.spacing.unit,
 	 },
    textField: {
        flexBasis: 200,
    },
    selectEmpty: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        flexGrow: 1,
    },
 })
class LoanWizard extends React.Component {
  
    state = {
    	search:''
    }

    handleOnChange = prop => event => {
    	this.setState({ [prop]: event.target.value })
    }
    handleSelect = event => {
        this.setState({ [event.target.name]: event.target.value });
    }
    handleFormSubmit = (values) => {
        console.log(values)

        // this.props.startLoading();
      }
    handleChange = panel => (event, expanded) => {

        (expanded)?this.props.expandPanel(panel):this.props.contractPanel(panel);
        
    }
    handleCalendar = (event) => {

        console.log(event)
        
    };
    
    renderField = ({ input, label, disabledProps, meta: { touched, error, warning } }) => {
      
      return (
          <div>
              <label>{'Descripcin'}</label>
              <div>
                  
                  <textarea {...input} placeholder="Content" rows="5" cols="40"></textarea>
                  {touched && ((error && <span>{error}</span>) || (warning && <span>{warning}</span>))}
              </div>
          </div>
      )
    }

    calculateEndTime=()=>{
        let {loan_type,loan_start_date} = this.props;
        if(!loan_start_date) return;
        loan_start_date = moment(loan_start_date);
        let end_date;
        switch(loan_type){
            case 0: //Diario
                end_date = loan_start_date.add(46,'days');
            break;
            case 1: //Semanal
                end_date = loan_start_date.add(13,'weeks');
            break;
            case 2: //Quincenal
                end_date = loan_start_date.add(3,'weeks');
            break;
            case 3: //Mensual
                end_date = loan_start_date.add(1,'month');
            break;
        }
        return loan_type + end_date.toString()
    }
  render() {
    const { classes,expanded } = this.props;
    console.log(this)
    
    
    return(
    <form  onSubmit={this.props.handleSubmit(this.handleFormSubmit).bind(this)}>
      	<Grid container alignItems={'center'}
            direction={'row'}
            justify={'space-around'} spacing={0} className={classes.root}>
            
            
            <Grid item container spacing={0} justify={'space-around'} xs={12}>
            
                <Grid item xs={8}>
                    <Paper>

                        <div style={{padding:'0 30px'}}>
                            <Field name="loan_type" component={renderField} 
                                  type="select" array={loanFrequency}
                                  label="Prstamo" />
                        </div>
                        <div style={{padding:'0 30px'}}>
                            <Field name="loan_amount" component={renderField} 
                                  type="text" normalize={onlyDigits}
                                  label="Monto Prstamo" value={0.0} startAdornment={
                                                <InputAdornment position="start">RD$</InputAdornment>
                                } />
                        </div>
                        <div style={{padding:'0 30px'}}>
                            <Field name="loan_rate" style={{width:150}} component={renderField} 
                                  className={classes.selectEmpty} type="text" normalize={onlyDigits}
                                  label="Tasa Prstamo" value={2.0} endAdornment={
                                                <InputAdornment position="end">%</InputAdornment>
                                } />
                        </div>
                        <div style={{padding:'0 30px'}}>
                            <Field name="loan_start_date" component={renderField} 
                                  type="date"   defaultValue={ moment() } value={moment()}
                                  endAdornment={
                                                <InputAdornment position="end"><Icon >today</Icon></InputAdornment>
                                    }
                                  label="Fecha Inicio" animation="slide-up"  />
                        </div>
                        
                        <div>
                            <div>Fecha Final</div>
                            {this.calculateEndTime()}
                        </div>
                
               
                
                    </Paper>
                        
                </Grid>
            </Grid>
            
            
            <Grid item xs={12}>
                <ExpansionPanel expanded={expanded === 'panel1'} onChange={this.handleChange('panel1')} >
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Grid container direction={'row'} >
                            <Grid item>
                                <Icon >bookmark_border</Icon>
                            </Grid>
                            <Grid item>
                                <Typography variant="title" color="inherit" noWrap>
                                Descripcn de garanta
                                </Typography>
                            </Grid>
                        </Grid>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Grid container direction={'row'} alignItems={'center'} justify={'space-around'}>
                            
                            <Grid item>
                                <Field name="loan_warranty_description" component={this.renderField} 
                                      label="Descripcn" normalize={upper}/>
                            </Grid>
                            
                            
                            
                        </Grid>
                    </ExpansionPanelDetails>

                </ExpansionPanel>

            </Grid>

      	</Grid>
    </form>

      )
  }
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({...Actions.stepper,...Actions.loading}, dispatch);
}

let stylesThemeComponent= withStyles(styles)(LoanWizard);
let FormFields = reduxForm({ form: 'createClients',
                             initialValues:{'loan_amount':0.0},
                             destroyOnUnmount: false,
                             forceUnregisterOnUnmount: true, 
                             validate:(v)=>validate(v,requiredFields),
                           })(stylesThemeComponent)

const selector = formValueSelector('createClients')

const mapStateToProps = (state) => {
  const { loan_start_date,
          loan_type,
             } = selector(state, 
        'loan_start_date',
        'loan_type',

    )
    const {panels,expanded} = state.stepper;
    return {
    authenticated: state.auth.authenticated,
    loading: state.loading.loading,
    panels,
    expanded,
    loan_start_date,
    loan_type
  };
};

const WizardGuarantor = connect(mapStateToProps,mapDispatchToProps)(FormFields);

export default WizardGuarantor ;


