import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Stepper, { Step, StepLabel } from 'material-ui/Stepper';
import Button from 'material-ui/Button';
import Typography from 'material-ui/Typography';
import {WizardClients} from '../components/CreateClientsContent';
import Clients from '../components/CreateClientsContent/Clients';
import Guarantor from '../components/CreateClientsContent/Guarantor';
import Loan from '../components/CreateClientsContent/Loan';
import { submit, destroy, getFormSubmitErrors } from 'redux-form';
import { connect } from 'react-redux'
import {bindActionCreators} from 'redux';
import * as Actions from '../actions';

const styles = theme => ({
  root: {
    width: '100%',
  },
  button: {
    marginRight: theme.spacing.unit,
  },
  instructions: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
  },
  buttonProgress: {
    color: 'blue',
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
});

function getSteps() {
  return ['Datos Cliente', 'Datos Fiador', 'Datos Prstamo','Crear Solicitud Prstamo'];
}
const getStepContent = (step,handler)=> {
      switch (step) {
        case 0:
          return <Clients onSubmit={handler } />;
        case 1:
          return <Guarantor onSubmit={handler }/>;
        case 2:
          return <Loan onSubmit={handler}></Loan>;
        default:
          return 'Unknown step';
      }
    }


class HorizontalLinearStepper extends React.Component {
    static propTypes = {
        classes: PropTypes.object,
    };

    state = {
        // activeStep: 0,
        skipped: new Set(),
    };

    isStepOptional = step => {
        return step === 4;
    };

    
    handleOnSubmit = (v)=>{
        // console.log(this.props)
    }
    isStepSkipped(step) {
        return this.state.skipped.has(step);
    }
    checkIfItIsPossible = ()=>{
        

        const { syncErrors } = this.props.form.createClients;

        if(syncErrors) {
            this.props.expandPanel('panel1');
            this.props.stopLoading();
            return true;
        }
        this.props.stopLoading();
        return false;
    }
    handleNext = () => {
        console.log(this.props)
        this.props.startLoading();
        this.props.submit('createClients')
        if (this.checkIfItIsPossible()) return
            // this.props.dispatch(destroy('createClients'));

        const { activeStep } = this.state;
        let { skipped } = this.state;
        if (this.isStepSkipped(activeStep)) {
          skipped = new Set(skipped.values());
          skipped.delete(activeStep);
        }
        this.props.changeStep(1);
        this.setState({
          // activeStep: activeStep + 1,
          skipped,
        });
    };

    handleBack = () => {
        this.props.startLoading();
        if (this.checkIfItIsPossible()) return

        this.props.backStep(-1);

        const { activeStep } = this.props;
        // this.setState({
        //   activeStep: activeStep - 1,
        // });
    };

    handleSkip = () => {
        const { activeStep } = this.props;
        if (!this.isStepOptional(activeStep)) {
          // You probably want to guard against something like this,
          // it should never occur unless someone's actively trying to break something.
          throw new Error("You can't skip a step that isn't optional.");
        }
        const skipped = new Set(this.state.skipped.values());
        skipped.add(activeStep);
        this.props.changeStep(1);
        this.setState({
          // activeStep: this.state.activeStep + 1,
          skipped,
        });
        };

        handleReset = () => {
            this.props.restStep();
        // this.setState({
        //   activeStep: 0,
        // });
    };

    render() {
    const { classes } = this.props;
    const steps = getSteps();
    const { activeStep,loading } = this.props;
    
    return (
      <div className={classes.root}>
      
        <Stepper activeStep={activeStep}>
          {steps.map((label, index) => {
            const props = {};
            const labelProps = {};
            if (this.isStepOptional(index)) {
              labelProps.optional = <Typography variant="caption">Optional</Typography>;
            }
            if (this.isStepSkipped(index)) {
              props.completed = false;
            }
            return (
              <Step key={label} {...props}>
                <StepLabel {...labelProps}>{label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <div>
          {activeStep === steps.length ? (
            <div>
              <Typography className={classes.instructions}>
                All steps completed - you&quot;re finished
              </Typography>
              <Button onClick={this.handleReset} className={classes.button}>
                Reset
              </Button>
            </div>
          ) : (
            <div>
              <div style={{overflowY:'scroll','overflow':'auto'}}>
                    {getStepContent(activeStep,this.handleOnSubmit)}
              </div>
              <div>
                <Button
                  disabled={activeStep === 0 || loading}
                  onClick={this.handleBack}
                  className={classes.button}
                >
                  Back
                </Button>
                {this.isStepOptional(activeStep) && (
                  <Button
                    variant="raised"
                    color="primary"
                    onClick={this.handleSkip}
                    className={classes.button}
                  >
                    Skip
                  </Button>
                )}
                <Button
                  variant="raised"
                  color="primary"
                  type="submit"
                  form='createClients'
                  disabled={loading}
                  onClick={this.handleNext}
                  className={classes.button}
                >
                  {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
    }
}

const StepperComponent = withStyles(styles)(HorizontalLinearStepper);
function mapDispatchToProps(dispatch) {
    const customActions ={
        submit: ()=>{
            return submit('createClients')
        }
    }
    return bindActionCreators({...Actions.loading,
                            ...Actions.stepper, ...customActions },dispatch);
}
const mapStateToProps = (state)=>{
    return {...state,
            activeStep:state.stepper.stepId,
            loading:state.loading.loading,
        } 
}

export default connect(mapStateToProps,mapDispatchToProps)(StepperComponent);


