import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../../actions';
import { Field, reduxForm ,formValueSelector  } from 'redux-form';

import { withStyles } from 'material-ui/styles';
import classNames from 'classnames';
import Typography from 'material-ui/Typography';

import Grid from 'material-ui/Grid';
import IconButton from 'material-ui/IconButton';
import MenuIcon from 'material-ui-icons/Menu';


import Icon from 'material-ui/Icon';

import Divider from 'material-ui/Divider';
import ExpansionPanel, {
  ExpansionPanelSummary,
  ExpansionPanelDetails,
} from 'material-ui/ExpansionPanel';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';
import validate from './validate';
import renderField from './renderField';

import Paper from 'material-ui/Paper';

import {upper, normalizePhone,onlyNums, onlyDigits,nationalID} from '../../utils/normalizer';
import {estadoCivil,local} from '../../utils/const';

const requiredFields = [
    'guarantor_name',
    

  ]

const styles = theme => ({

	margin: {
    	margin: theme.spacing.unit,
 	 },
    textField: {
        flexBasis: 200,
    },
    selectEmpty: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        flexGrow: 1,
    },
 })
class CreateClientsContent extends Component {
  
    state = {
    	search:''
    }

    handleOnChange = prop => event => {
    	this.setState({ [prop]: event.target.value })
    }
    handleSelect = event => {
        this.setState({ [event.target.name]: event.target.value });
    }
    handleFormSubmit = (values) => {
        console.log(values)

        // this.props.startLoading();
      }
    handleChange = panel => (event, expanded) => {

        (expanded)?this.props.expandPanel(panel):this.props.contractPanel(panel);
        
    };
    
    renderField = renderField

  render() {
    const { classes, guarantor_place_status ,guarantor_marital_status,expanded } = this.props;
    console.log(this)

    return(
    <form  onSubmit={this.props.handleSubmit(this.handleFormSubmit).bind(this)}>
      	<Grid container alignItems={'center'}
            direction={'row'}
            justify={'space-around'} spacing={0} className={classes.root}>
            
            
            <Grid item xs={12}>
                <ExpansionPanel expanded={expanded === 'panel1'} onChange={this.handleChange('panel1')} >
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Grid container direction={'row'} >
                            <Grid item>
                                <Icon >face</Icon>
                            </Grid>
                            <Grid item>
                                <Typography variant="title" color="inherit" noWrap>
                                Datos del Fiador
                                </Typography>
                            </Grid>
                        </Grid>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Grid container direction={'row'} alignItems={'center'} justify={'space-around'}>
                            
                            <Grid item>
                                <Field name="guarantor_name" component={this.renderField} 
                                      classess={classes} type="text" 
                                      label="Nombre(s) y Apellido(s)" icon="perm_identity" normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_age" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Edad" icon="perm_identity" normalize={onlyNums}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_national_id" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Cdula" icon="perm_identity" normalize={nationalID}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_address" component={this.renderField} 
                                      classess={classes} type="text" 
                                      label="Direccin"  normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_marital_status" component={this.renderField} 
                                       classess={classes} type="select" array={estadoCivil} 
                                       label="Estado Civil"  />
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_place_status" component={this.renderField} 
                                       classess={classes} type="select" array={local}
                                       label="Local"  />
                            </Grid>
                            <Grid item>
                                <Field disabledProps={guarantor_place_status} name="guarantor_rent" component={this.renderField} 
                                       classess={classes} type="text" normalize={onlyDigits}
                                       label="Precio Alquiler"  />
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_business_name" component={this.renderField} 
                                       classess={classes} type="text" normalize={upper}
                                       label="Nombre Negocio"  />
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_business_phone" component={this.renderField} 
                                       classess={classes} type="text" normalize={normalizePhone}
                                       label="Telfono Negocio"  />
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_business_address" component={this.renderField} 
                                       classess={classes} type="text" normalize={upper}
                                       label="Direccin Negocio"  />
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_business_owner" component={this.renderField} 
                                       classess={classes} type="text" normalize={upper}
                                       label="Propietario del Negocio"  />
                            </Grid>
                            
                        </Grid>
                    </ExpansionPanelDetails>

                </ExpansionPanel>

            </Grid>
            <Grid item xs={12}>
                <ExpansionPanel expanded={expanded === 'panel2'} onChange={this.handleChange('panel2')}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Grid container direction={'row'} >
                            <Grid item>
                                <Icon >assignment_ind</Icon>
                            </Grid>
                            <Grid item>
                                <Typography variant="title" color="inherit" noWrap>
                                Referencia Personales del Fiador
                                </Typography>
                            </Grid>
                        </Grid>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Grid container direction={'row'} alignItems={'center'} justify={'space-around'}>
                            
                            <Grid item>
                                <Field name="guarantor_ref1_name" component={this.renderField} 
                                      classess={classes} type="text" 
                                      label="Nombre(s) y Apellido(s) Referencia1"  normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_ref1_address" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Direccin Referencia1" normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_ref1_phone" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Telfono Referencia1"  normalize={normalizePhone}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_ref2_name" component={this.renderField} 
                                      classess={classes} type="text" 
                                      label="Nombre(s) y Apellido(s) Referencia2"  normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_ref2_address" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Direccin Referencia2" normalize={upper}/>
                            </Grid>
                            <Grid item>
                                <Field name="guarantor_ref2_phone" component={this.renderField} 
                                       classess={classes} type="text" 
                                       label="Telfono Referencia2"  normalize={normalizePhone}/>
                            </Grid>
                            
                            
                        </Grid>
                    </ExpansionPanelDetails>

                </ExpansionPanel>

            </Grid>
      	</Grid>
    </form>

      )
  }
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({...Actions.stepper,...Actions.loading}, dispatch);
}

let stylesThemeComponent= withStyles(styles)(CreateClientsContent);
let FormFields = reduxForm({ form: 'createClients',                         
                             destroyOnUnmount: false,
                             forceUnregisterOnUnmount: true, 
                             asyncValidate:(v)=>validate(v,requiredFields),
                           })(stylesThemeComponent)

const selector = formValueSelector('createClients')

const mapStateToProps = (state) => {
  const { guarantor_marital_status,
            guarantor_place_status,
             } = selector(state, 
        'guarantor_marital_status',
        'guarantor_place_status',

    )
    const {panels,expanded} = state.stepper;
    return {
    authenticated: state.auth.authenticated,
    loading: state.loading.loading,
    guarantor_marital_status,
    guarantor_place_status,
    panels,
    expanded
  };
};

const WizardGuarantor = connect(mapStateToProps,mapDispatchToProps)(FormFields);

export default WizardGuarantor ;


