


const validate = async (values,requiredFields) => {
  // console.log(values)
  const errors = {};

  requiredFields.forEach(field => {

      let stringify = (typeof values[field] ==='undefined')?values[field]:values[field].toString();
      if (!stringify) {
        errors[field] = 'Este campo es requerido'
      }
  })

  if(values.national_id){
    if (values.national_id.length < 13 ) {
        errors.national_id = "Cdula incorrecta";
      } 
  }
  
  if(values.phone){
    if (values.phone.length < 12) {
        errors.phone = "Telfono incorrecto";
      }
  }
  

  return errors;
};

export default validate;


