const requiredFields = [
    'first_name',
    'second_name',
    'first_surname',
    'second_surname',
    'national_id',
    'phone',
    'address',
    'marital_status'
  ]
  
const validate = values => {
  const errors = {};

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Este campo es requerido'
    }
  })

  if(values.national_id){
    if (values.national_id.length < 13 ) {
        errors.national_id = "Cdula incorrecta";
      } 
  }
  
  if(values.phone){
    if (values.phone.length < 12) {
        errors.phone = "Telfono incorrecto";
      }
  }
  

  return errors;
};

const selectField = ({input,classes,array})=>{
    // console.log(input,array)
    let propClass = classes;
    if(!classes){
        propClass = {}
    }
    return(
        <Select
            value={input.value}
            onChange={(event) => {console.log(input);input.onChange(event.target.value)} }
            displayEmpty
            name={input.name}
            className={propClass}
          >
            {array.map( item =>
                        <MenuItem key={item.text+item.value} value={item.value}>{item.text}</MenuItem>
            )}
          </Select>

        )
} 

const styles = theme => ({

	margin: {
    	margin: theme.spacing.unit,
 	 },
    textField: {
        flexBasis: 200,
    },
    selectEmpty: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        flexGrow: 1,
    },
 })
