import React from 'react';
import Grid from 'material-ui/Grid';
import Select from 'material-ui/Select';
import { FormControl, FormHelperText } from 'material-ui/Form';
import Input, { InputLabel, InputAdornment } from 'material-ui/Input';
import Menu, { MenuItem } from 'material-ui/Menu';
import DatePicker from 'rc-calendar/lib/Picker';
import Calendar from 'rc-calendar';
import moment from 'moment';

const selectField = ({input,rest,array})=>{
    // console.log(input,array)
    
    return(
        <Select
            {...input}
            displayEmpty
            
            {...rest}
          >
            {array.map( item =>
                        <MenuItem key={item.text+item.value} value={item.value}>{item.text}</MenuItem>
            )}
          </Select>

        )
} 

const datePicker = ({input,rest})=>{
    
    const calendar = (<Calendar />)
    let {onChange,value} = input;
    console.log(value);
    if(!value || typeof value === 'string'){
        value = moment(value);
    }
    
    return (<DatePicker {...rest} calendar={ calendar } value={value}  onChange={onChange} >
        { ({value})=>{
            return (
                
                <Input

                  style={{ width: 150 }}
                  
                  readOnly
                  endAdornment={rest.endAdornment}
                  className=""
                  value={value && value.format('DD-MMM-YYYY' || '')}
                />
                
                )
        } } 
        </DatePicker>
    )
}


const renderField = (fields) => {
    // console.log(fields)
    const { input, label, type, disabledProps,array, meta: { touched, error }, ...rest } = fields;
      let renderComponent = {}
        switch (type) {
            case 'select':
                renderComponent = selectField({rest,array,input});
                break;
            case 'date':
                renderComponent = datePicker({input,rest});
                break;
            default:
                renderComponent =  (<Input {...rest}
                  disabled={ (disabledProps)?true:false} 
                  id={label} {...input} type={type} />)


        }

      return (
      <Grid container spacing={8} alignItems="center">
        
        <Grid item>
          <FormControl  error={ (touched && error)?true:false } aria-describedby="text-field">
            <InputLabel htmlFor={label}>{label}</InputLabel>
            { renderComponent }

            <FormHelperText id="name-helper-text">{touched && (error && <span>{error}</span>) }</FormHelperText>
          </FormControl>
        </Grid>
      </Grid>
      )
    }

export default renderField;


